@use '@mview/scss/utilities/functions' as *;

.topbar-old-wrapper {
  position: sticky;
  top: 0px;
  background-color: pick-color('secondary', 'steam');
  z-index: 200;

  img {
    height: 32px;
  }

  .nav-wrapper {
    margin-top: 8px;
    color: white;

    .nav-top {
      background-color: pick-color('primary', 'jet');
      display: flex;
      justify-content: space-between;
      align-items: center;

      .nav-top-list {
        display: flex;
        align-items: center;

        .top-list-item {
          list-style: none;
          padding: 0 16px;
          font-size: 14px;

          &:first-child {
            padding: 10px 26px;
            // background: linear-gradient(180deg, #00aeef 0%, #1d417e 100%);
            background-color: pick-color('primary', 'blue');
          }
        }
      }

      .nav-top-user {
        display: flex;
        align-items: center;
        padding-right: 24px;

        h6 {
          font-size: 14px;
          margin: 0;
        }

        mat-icon {
          font-size: 20px;
          width: 20px;
          height: 20px;
          color: white;
          margin-left: 8px;
        }
      }
    }

    .nav-bottom {
      // background: linear-gradient(180deg, #00aeef 10%, #1d417e 100%);
      border-top: 1px solid white;
      background-color: pick-color('primary', 'blue');
      display: flex;
      align-items: center;
      padding: 8px;

      .nav-bottom-list {
        display: flex;
        align-items: center;

        .bottom-list-item {
          list-style: none;
          padding: 0 16px;
          color: black;
          font-size: 14px;

          &:first-child {
            padding-left: 10px;
            color: white;
          }
        }
      }
    }
  }
}
